.car{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 60px;
    min-height: 85vh;
    height: auto;
}

.car > img{
    height: 350px;
}

.carContent{
    flex: 1
}

.carTitle{
    font-size: 40px;
    font-weight: 600;
    margin-bottom: 20px;
}

.otherDetails > p{
    color: gray;
    margin: 10px 0;
}

.otherDetails{
    margin-bottom: 30px;
}

.reservation{
    padding: 80px 100px;
}

.reservation > button{
    display: block;
    margin: 0 auto;
    font-size: 16px;
    margin-top: 20px;
}

.form{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
}

.formTitle{
    font-size: 40px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 50px;
}

.inputGroup > p{
    color: gray;
    font-size: 13px;
    margin-top: 10px;
}

.inputGroup > input{
    background-color: rgba(0, 0, 0, 0.2);
    border: 0;
    padding: 5px;
    font-size: 16px;
    width: 100%;
    font-family: 'Inter', sans-serif;
    margin: 5px 0 20px 0;
    border-bottom: 1px dashed gray;
    outline: none;
    color: #f1f1f1;
    transition: 0.2s all linear;
}

.inputGroup > input::placeholder{
    font-size: 14px;
}

.inputGroup > input:focus{
    border-bottom: 1px dashed var(--primary-yellow);
}

@media screen and (max-width: 468px) {
    .car{
        flex-direction: column;
        padding: 20px;
    }
    .car > img{
        height: 250px;
    }
    .reservation{
        padding: 20px;
    }
    .form{
        grid-template-columns: repeat(1, 1fr);
        gap: 0px;
    }
}