.footer{
    background-color: rgb(26, 25, 25);
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 30px 0;
}

.footer > p{
    font-size: 40px;
    font-weight: 700;
}

.footerLinks{
    display: flex;
    margin: 20px 0;
}

.footerLinks > p{
    color: gray;
    margin-right: 15px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.footerLinks > p:hover{
    color: #f1f1f1;
}

.copyright{
    padding: 15px 0;
    background-color: rgb(19, 17, 17);
    display: flex;
    align-items: center;
    justify-content: center;
}

.copyright > p{
    font-size: 14px;
    color: rgb(128, 126, 126);
}